<template>
  <div class="ImgList" ref="ListWrapper" :style="{height: clientHeight+'px'}">
    <ul>
      <li class="serach_first" style="border: none">
        <div class="iptSearch" v-if="reservationBr">
          <input @change="iptChange" v-model="searchName" type="text" placeholder="请输入场馆名称">
        </div>
        <div v-else class="btnTimeList">
          <div class="timeList">
            <div class="statTime">开始时间 <input type="text" placeholder="请选择" ref='ysEnd' class="ys-end"></div>
            <div class="LengthUse">使用时长 
              <select v-model="selectChageTxt" name="" class="lengthUseList">
                <option value="">请选择</option>
                <option v-for="(item, index) in timeList" :key="index" :value="item.key" >{{item.name}}</option>
              </select>
              <input v-if='selectChageTxt=="customize"' type="text" ref="customizeipt" class="customizeipt" placeholder="自定时间段">
            </div>
          </div>
          <div class="btnSearch" @click='Inquire'>查询</div>
        </div>
      </li>
      <li class="errorlist" v-if="!ImgLists.length"><div class="error"></div></li>
      <li v-for="(item, index) in ImgLists" :key="index">
        <router-link :to='{name: "AppointmentDetails", params: {paths: reservationBr?"venuePriority": "timePriority",item: item}}'>
          <img :src="item.cover" alt="">
        </router-link>
        <div class="ImgLIst-right">
          <router-link :to='{name: "AppointmentDetails", params: {paths: reservationBr?"venuePriority": "timePriority", item: item}}'>
            <h3>{{item.roomName}}</h3>
            <p class="address">{{item.address}}</p>
            <p class="holder">可容纳<span class="capacity">{{item.number}}</span>人</p>
          </router-link>
          <p class="btn"><router-link :to="{name: 'submitReservation', params: {item}}">预约</router-link></p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from 'axios'
import BScroll from 'better-scroll'
import { setTimeout } from 'timers'
import jeDate from "@static/js/jedate/jquery.jedate.min.js"
import "@static/js/jedate/skin/jedate.css"
export default {
  name: 'ImgList',
  data () {
    return {
      clientHeight: '',
      ImgLists: [],
      searchName: '',
      timeList: [
        {name: '15分钟', key: '0.25'},
        {name: '半小时', key: '0.5'},
        {name: '1小时', key: '1'},
        {name: '2小时', key: '2'},
        {name: '4小时', key: '4'},
        {name: '8小时', key: '8'},
        {name: '自定义', key: 'customize'}
      ],
      selectChageTxt: '',
      stateTimeTxt: '',
      time: '',
      startTime: '',
      page: 0,
      size: 10,
      scrollBooler: false,
      totalPagesNum: 0
    }
  },
  props: {
    reservation: String
  },
  // 挂载结束状态
  mounted: function () {
    $(".ys-end").jeDate({
      skinCell: "jedategreen",
      isinitVal: true,
      festival: false,
      initAddVal: [2, "hh"],
      format: 'YYYY-MM-DD hh:mm'
    });
  },
  created () {
    this.venuePpriorityList()
    this.$nextTick ( () => {
      this.InitTabScroll ();
    });
  },
  methods: {
    Inquire () {
      const wu = this.$Toast
      const text = this.selectChageTxt
      if(text == 'customize') {
        const value = this.$refs.customizeipt.value
        if(!value) {
          wu.showToast({
            title: '使用时长不能为空！',
            mask: false,
            icon: 'icon-success', // icon-success | icon-error | icon-info
            duration: 3000
          });
          return
        }
        this.time = value
      }else{
        if(!text) {
          wu.showToast({
            title: '使用时长不能为空！',
            mask: false,
            icon: 'icon-success', // icon-success | icon-error | icon-info
            duration: 3000
          });
          return
        }
        this.time = text
      }
      const stateValue = this.$refs.ysEnd.value
      if(!stateValue) {
          wu.showToast({
            title: '使用时长不能为空！',
            mask: false,
            icon: 'icon-success', // icon-success | icon-error | icon-info
            duration: 3000
          });
          return
      }
      this.startTime = stateValue
      this.venuePpriorityList()
    },
    iptChange () {
      this.venuePpriorityList(this.searchName)
    },
    venuePpriorityList (searchNames) {
      const me = this
      const params = {
        params: {
          page: this.page,
          size: this.size
        }
      }
      if(this.time) {
        params.params.time = this.time
      }
      if(this.startTime) {
        params.params.startTime = this.startTime
      }
      if(searchNames){
        params.params.name = searchNames
      }
      axios.get('iapp/api/app/boardroom/detail/byTime', params)
      .then(res => {
        const data = res.data
        if(this.totalPagesNum+1 > data.totalPages) {
         me.scrollBooler = false 
        }
        if(me.scrollBooler) {
          data.content.map( i => {
            me.ImgLists.push(i)
          })
        }else{
          me.ImgLists =data.content
        }
        setTimeout(() => {this.searchName = ''}, 3500)
      })
    },
    InitTabScroll () {
      const me = this
      this.clientHeight = `${document.documentElement.clientHeight}`-45
      this.$nextTick(()=>{
        if (!this.scroll) {
          this.scroll=new BScroll(this.$refs.ListWrapper, {
            scrollY: true,
            click:true,
        });
      }else{
        this.scroll.refresh()
      }
      this.scroll.on('touchEnd', (pos) => {
        if(pos.y < -385){
          me.page = me.page+1
          if(!me.scrollBooler) {
            me.scrollBooler = true
            me.venuePpriorityList()
          }
        }
      })
     });
    }
  },
  computed: {
    reservationBr () {
      return this.reservation == "场馆优先"? true : false
    }
  },
}
</script>
<style lang="less" scoped>
.ImgList {
  overflow: hidden;
  li {
    border-bottom: 1px solid #f1f1f1;
    display: flex;
    padding: .8125rem  1.25rem;
    background: #fff;
    .btnTimeList{
      width: 100%;
    }
    .timeList {
      width: 100%;
      background: #fff;
      .statTime,.LengthUse {
        border-bottom: 1px solid #dbdbdb;
        padding: .625rem;
        position: relative;
        // span {
        //   position: absolute;
        //   right: 1.875rem /* 30/16 */;
        // }
      }
      .customizeipt {
        text-align: right;
        position: absolute;
        right: 1.9375rem /* 31/16 */;
        top: .75rem /* 12/16 */;
        background: #fff;
      }
      .ys-end {
        width: 70%;
        background: #fff;
        text-align: center;
      }
      .lengthUseList {
        background: #fff;
        width: 30%;
        float: right;
        border: none;
        outline: none;
        padding-left: 1.875rem;
      }
    }
    .btnSearch {
      width: 40%;
      text-align: center;
      margin: 5% auto;
      padding: .625rem 0;
      background: #1f95ff;
      border: 1px solid #1f95ff;
      border-radius: .3125rem /* 5/16 */;
      color: #fff;
    }
    &.serach_first {
      padding: 0;
      background: #f5f7fa;
      .iptSearch {
        margin: .8125rem  1.25rem;
        width: 100%;
        background: #fff;
        border: 1px solid #dbdbdb;
        border-radius: 1.25rem;
        height: 2rem /* 32/16 */;
        overflow: hidden;
        position: relative;
        &::before {
          content: " ";
          display: block;
          position: absolute;
          width: 16px;
          height: 20px;
          background: url(/iapp/v1.0.01/img/search.png) no-repeat;
          background-size: 16px;
          right: .75rem /* 12/16 */;
          top: 50%;
          margin-top: -8px;
        }
        input {
          width: 90%;
          height: 100%;
          background: #fff;
          text-indent: 1rem; /* 16/16 */
        }
      }
    }
    img {
      width: 5rem;
      height: 100%;
    }
    .ImgLIst-right {
      flex: 1;
      margin-left: .75rem;
      position: relative;
      h3, p {
        color: #333;
        margin-bottom: .25rem /* 4/16 */;
      }
      p {
        color: #333;
      }
      .holder::before,
      .address::before {
        content: '';
        display: inline-block;
        width: 1rem;
        height: 1rem;
        margin-right: .375rem;
        vertical-align: text-top;
      }
      .address::before {
        background: url(/static/images/address.png)no-repeat center;
        background-size: contain;
      }
      .holder {
        color: #b6b6b6;
        &::before{
          background: url(/static/images/number_people.png)no-repeat center;
          background-size: contain;
        }
      }
      p.btn {
        border: 1px solid #1f95ff;
        padding: .1875rem /* 3/16 */ 1.125rem /* 18/16 */;
        border-radius: .375rem /* 6/16 */;
        position: absolute;
        bottom: 0;
        right: 0;
        a {
          color: #1f95ff;
        }
      }
    }
     &.errorlist {
      border: none;
      margin-top: 30%;
      background: #f5f7fa;
      .error {
        width: 6.25rem;
        height: 6.25rem;
        background: url(/static/images/nulldata.png) no-repeat center;
        background-size: contain;
        margin: 0 auto;
      }
    }
  }
}
</style>




